<template>
	<view class="wrapper">
		<view class="login-wrap">
			<!-- <navigator url="https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx1c9d77f2a708e46d&redirect_uri=http%3a%2f%2f4103f0k649.zicp.vip/pages/blindbox-show/blindbox-show&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect" class="btn-login">微信登录</navigator> -->
			<view class="btn-login" @click="login()">微信登录</view>
			<view class="tips">
				登录注册即表示同意<navigator url="" class="link">用户协议</navigator>与<navigator url="" class="link">隐私条款</navigator>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				isWeixin: false,
			}
		},
		onLoad() {
			
			this.isWechat();

		},
		methods: {
			isWechat() {
				//判断是否是微信端
				var ua = navigator.userAgent.toLowerCase();
				var isWeixin = ua.indexOf('micromessenger') != -1;
				if (!isWeixin) {
				   document.head.innerHTML = '<title>抱歉，出错了</title><meta charset="utf-8"><meta name="viewport" content="width=device-width, initial-scale=1, user-scalable=0"><link rel="stylesheet" type="text/css" href="https://res.wx.qq.com/open/libs/weui/0.4.1/weui.css">';
				   document.body.innerHTML = '<div class="weui_msg"><div class="weui_icon_area"><i class="weui_icon_info weui_icon_msg"></i></div><div class="weui_text_area"><h4 class="weui_msg_title">请在微信客户端打开链接</h4></div></div>';
				   
				   return;
				}
			},
			
			login() {
				console.log("登录跳转...");
					
					let url = encodeURIComponent('http://21471yw772.51mypc.cn/#/pages/blindbox-show/blindbox-show');
				    window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx1c9d77f2a708e46d&redirect_uri=${url}&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect`;
					

			}
		}
	}
</script>

